.cell {
	min-width: 10px !important;
}
.timeContainer {
	max-width: 300px;
	min-width: 260px;
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	padding: 10px;
	font-size: 15px;
	font-family: Roboto;
}

.time_day-name {
	display: flex;
	justify-content: space-between;
}

.time_day-name__items {
	margin: 0 10px 10px;
}

.time_day__weekday {
	font-size: 1.3em;
}
.time_day-slider {
	color: rgb(182, 182, 182);
	align-items: center;
}

.time_day__button-calendar {
	color: rgb(182, 182, 182);
	width: 20px;
	height: 20px;
	cursor: pointer;
}
.time_day__date {
	margin-right: auto;
	color: #000;
}

.time_day-block {
	display: flex;
	justify-content: space-between;
}

.time_day__button {
	outline: none;
	margin: 0 10px;
	cursor: pointer;
	text-transform: uppercase;
}

/* .time_day__button:hover {
    color: #1f9bff
} */
.time_day__button-confirm {
	color: #1f9bff;
}
.time_day__button-cancel {
	color: #000;
}
.time_day__button-disabled {
	color: rgb(144, 144, 144);
}

.time_day_confirm-block {
	justify-content: flex-end;
	margin-bottom: 10px;
}

.time_grid__item {
	padding: 15px;
	border-radius: 25px;
	font-size: 14px;
	width: 20px;
	height: 20px;
	display: flex;
	justify-content: center;
	align-items: center;
	cursor: pointer;
	float: left;
	font-weight: bold;
	background-color: rgb(232, 232, 232);
}
.time_grid__item-selected {
	background: #4a90e2;
	color: #fff;
}
.time_grid {
	margin: 20px;
	min-height: 250px;
}
.empty {
	display: flex;
	justify-content: center;
	align-items: center;
}
.emptyDay_shedule {
	display: inline-block;
}

.time_grid__item-disable {
	color: grey;
	cursor: not-allowed;
}
