.circle {
	width: 32px;
	height: 32px;
	min-width: 32px;
	color: #fff;
	border-radius: 50%;
	background-image: linear-gradient(to top, #22c3ff, #1c72ff);
	font-family: Helvetica;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 10.9px;
}
.timeTag {
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-radius: 15.7px;
	background-color: #f8f8f8;
	height: 32px;
	margin: 10px 15px 3px 0;
	.time {
		height: 32px;
		font-family: Helvetica Neue;
		display: flex;
		justify-content: space-between;
		align-items: center;
		margin: 10px;
		font-size: 12px;
	}
}
