$maxWidth: 80vw;

.container {
	min-width: 300px;
	padding: 20px 25px;
	max-height: 80vh;
	max-width: $maxWidth;

	.buttonContainer {
		padding-top: 25px;
		display: flex;
		justify-content: flex-end;
		// max-height: 20px;
	}
	.dataContainer {
		overflow: auto;
		// min-width: 300px;
		padding: 20px 25px;
		max-height: calc(80vh - 125px);
		max-width: $maxWidth;
	}
	.button {
		margin-left: 20px;
		font-size: 0.8em;
	}
}
